/* App baseline */
div.MDKPage {
  background-color: #FFFFFF;
  color: #1A1A1A;
}
/* Action Bar */
ui5-mdk-bar.actionbar {
  background-color: #E10600;
  color: #FFFFFF;
}
ui5-mdk-bar.actionbar Title {
  color: #FFFFFF;
}
ui5-mdk-bar.actionbar Icon {
  color: #FFFFFF;
}
/* Buttons */
Button {
  background-color: #E10600;
  color: #FFFFFF;
  border-radius: 10;
}
/* Form cells (labels/values) */
FormCell Label {
  color: #1A1A1A;
}
FormCell Value {
  color: #1A1A1A;
}
ObjectCell Title {
  color: #1A1A1A;
}
ObjectCell Status {
  color: #E10600;
}
/* Search bar: use color only (safe) */
